<template>
  <div class="login-container">
    <div class="login-card">
      <div class="title">
        <img src="@/assets/img/logo.png" />
      </div>
      <div class="content">
        <el-form
          ref="loginFormRef"
          :model="loginFormData"
          :rules="loginFormRules"
          @keyup.enter="handleLogin"
        >
          <el-form-item prop="username">
            <el-input
              v-model.trim="loginFormData.username"
              placeholder=""
              type="text"
              tabindex="1"
              :prefix-icon="User"
              size="large"
            />
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              v-model.trim="loginFormData.password"
              placeholder=""
              type="password"
              tabindex="2"
              :prefix-icon="Lock"
              size="large"
              show-password
            />
          </el-form-item>
          <el-button
            :loading="loading"
            type="primary"
            size="large"
            @click.prevent="handleLogin"
            > </el-button
          >
        </el-form>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { reactive, ref } from "vue";
import { useRouter } from "vue-router";
import { type FormInstance, FormRules } from "element-plus";
import { User, Lock } from "@element-plus/icons-vue";
import { loginApi } from "../api/login";
import { setCookie, setToken } from '../utils/cache/cookies'

export default {
  components: {
  },
  setup() {
    const router = useRouter();
    /**  */
    const loginFormData: any = reactive({
      username: "admin",
      password: "12345678",
    });
    /**  */
    const loginFormRules: FormRules = {
      username: [{ required: true, message: "", trigger: "blur" }],
      password: [
        { required: true, message: "", trigger: "blur" },
        { min: 8, max: 16, message: " 8  16 ", trigger: "blur" },
      ],
      code: [{ required: true, message: "", trigger: "blur" }],
    };

    /**  */
    const loginFormRef = ref<FormInstance | null>(null);
    /**  Loading */
    const loading = ref(false);

    /**  */
    const handleLogin = () => {
      loginFormRef.value?.validate(async (valid: boolean, fields: any) => {
        if (valid) {
          loading.value = true;
          try {
            let res = await loginApi(loginFormData)
            loading.value = false;
            if (!res.success) {
              return
            }
            setCookie('userId', res.model.userId || "")
            setToken(res.model.token || "")
            window.location.href = '/'
          } catch (e) {
            loading.value = false;
          }
          
        } else {
          console.error("", fields);
        }
      });
    };

    return {
      loginFormData,
      loginFormRules,
      handleLogin,
      User,
      Lock,
      loading,
      loginFormRef,
    };
  },
};
</script>

